import { AUTOSPIN_INTERFACE_BUTTONS } from '../../../../constants/autospinInterfaceButton/autospin-interface-buttons.constant'
import { AUTOSPIN_VALUES }            from '../../../../constants/autospin-values/autospin-values.constant'
import { AutospinInterfaceButton }    from '../button/autospinInterface.button.class'
import { Click }                      from '../../../click/click.class'

/**
 * @file Defines the {@link AutospinMinus} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /**
  * Class for handling minus button in autospin interface
  * @extends AutospinInterfaceButton
  */
  export class AutospinMinus extends AutospinInterfaceButton {
 
     constructor() {
         super(AUTOSPIN_INTERFACE_BUTTONS.minus) //Call parent constructor
     }
 
     /** Adds click handler to minus button */
     add() {
 
         let minus = document.getElementById(AUTOSPIN_INTERFACE_BUTTONS.minus.id) //Retrieve minus button node
         new Click(minus, this.click, this)                                       //Add click handler
         this._added = true                                                       //Update click handler flag
         
     }
 
     /**
      * Click handler for decreasing autospin amount
      * @param {object} this_ - Reference to class's scope
      */
     click(this_) {
         
         AUTOSPIN_VALUES.decrease() //Decrease autospin amount
         super.click()              //Call super click
 
     }
 
  }