import { EVENTS } from '../../constants/events/events.constant'

/**
 * @file Defines the {@link StateClick} class
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /** Class for handling adding different event handlers depending on buttons state */
 export class StateClick {

    /**
     * Attach click event handler to an element
     * @param {HTMLElement}        elem_   - Reference to HTML element
     * @param {string}             id_     - Button ID
     * @param {state-click-events} click_  - Object containg click events for each state
     * @param {object}             this_   - Reference to class's scope
     */
    constructor(elem_, id_, click_, this_) {

        let handler = ((e_) => {

            //If touch event
            if(e_.type === 'touchstart') {
                e_.preventDefault() //Prevent click event firing as well as touch
            }

            //Retrieve button state
            let state = window.newUI[id_]().state.toString().toLowerCase()

            //Execute correct click function based on state
            click_[state]()
            
        })

        //For each event
        EVENTS.forEach((e_) => {
            elem_.addEventListener(e_, handler) //Add handler event
        })

    }

 }