import { Click }                 from '../../../click/click.class'
import { BET_VALUES }            from '../../../../constants/bet-values/bet-values.constant'
import { BetInterfaceButton }    from './betInterface.button.class'
import { BET_INTERFACE_BUTTONS } from '../../../../constants/betInterfaceButton/bet-interface-buttons.constant'

/**
 * @file Defines the {@link BetMinus} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /** 
  * Class for handling minus button in bet interface
  * @extends BetInterfaceButton
  */
 export class BetMinus extends BetInterfaceButton {

    /**
     * Initialise minus button content
     */ 
    constructor() {
        super(BET_INTERFACE_BUTTONS.minus) //Call parent constructor
    }

    /** Adds click handler to minus button */
    add() {

        let minus = document.getElementById(BET_INTERFACE_BUTTONS.minus.id) //Retrieve minus button node
        new Click(minus, this.click, this)                                  //Add click handler
        this.added = true                                                   //Update click handler flag

    }

    /**
     * Click handler for decreasing bet value
     * @param {object} this_ - Reference to class's scope
     */
    click(this_) {
 
        BET_VALUES.decrease() //Decrease bet value
        super.click()         //Call super click
 
    }

 }