import { Click }                      from '../../../../click/click.class'
import { BetInterfaceSettingsButton } from './betInterface.button.settings.class'
import { BET_INTERFACE_BUTTONS }      from '../../../../../constants/betInterfaceButton/bet-interface-buttons.constant'

/**
 * @file Defines the {@link Coins} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /** 
  * Class for handling coins button in bet interface
  * @extends BetInterfaceSettingsButton
  */
 export class Coins extends BetInterfaceSettingsButton {

    /**
     * Initlaise coins button
     */
    constructor() {
        super(BET_INTERFACE_BUTTONS.coins) //Call parent constructor
    }

    /** Adds click handler to coins button */
    add() {

        let coins = document.getElementById(BET_INTERFACE_BUTTONS.coins.id) //Retrieve coins button node
        new Click(coins, this.click, this)                                  //Add click handler
        this.added = true                                                   //Update click handler flag

    }

    /**
    * Click handler for coin button
    * @param {object} this_ - Reference to class's scope
    */
     click(this_) {

        //If game is displaying in cash
        if (!balanceManager_.getShowIncredits()) {

            document.getElementById(BET_INTERFACE_BUTTONS.cash.id).classList.add('off') //Add button setting off styling to cash button
            this_.elem.classList.remove('off')                                          //Remove button setting off styling to cash button
            super.click()                                                               //Call super click

        }

     }

 }