/**
 * Returns whether or not localStorage supported
 * @returns {boolean}
 */
export function localStorageSupported() {

    let foo = 'foo' //Placeholder key
    let bar = true  //Placeholder value

    //Try and store placeholder in localStorage
    try {

        localStorage.setItem(foo, bar)  //Store placeholder
        localStorage.removeItem(foo)    //Remove placeholder
        return true                     //Return localStorage supported
        
    }

    //localStorage not supported
    catch(e) {
        return false
    }

}