import { BetInterface } from '../interface/betInterface/betInterface.class'
import { Button }       from './button.class'
import { Click }        from '../click/click.class'
import { arch, hidden } from '../../sass/button/arch/button.arch.class.scss'
import { IS_IE }        from '../../constants/isIE/is-ie.constant'

/**
 * @file Defines the {@link BetButton} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /**
  * Class for handling bet button
  * @extends Button
  */
 export class BetButton extends Button {

    /**
     * Reference to bet interface
     * @memberof BetButton
     * @protected
     * @instance
     * @type {BetInterface}
     */
    _interface

    /**
     * Initialise core button properties
     * @param {string[]} assets_  - Array of data URIs containing button assets.
     * @param {string}   id_      - ID of button.
     * @param {string[]} states_  - Array of possible button states.
     * @param {boolean}  visible_ - Whether button is initially visible.
     * @param {boolean}  text_    - Whether button has text.
     * @param {function} click_   - Object containg click events for each state.
     */
    constructor(assets_, id_, states_, visible_, text_, click_ = null) {

        super(assets_, id_, states_, visible_, text_) //Call parent constructor

        //If mobile
        if (framework.isTouch()) {

            this._interface = new BetInterface()    //Create bet interface
            new Click(this._elem, this.click, this) //Add click handler

        }

        //Desktop
        else {

            //If button has click handler
            if (this.click !== null) {
                new Click(this.elem, click_, this) //Add click handler
            }

            //If button has text
            if (text_) {

                //Loop through buttons children nodes
                for (let i = 0; i < this.elem.children[0].children.length; i++) {

                    let c = this.elem.children[0].children[i] //Store child node

                    //If text node found
                    if (c.className === 'bet__amount') {
                        this._textElem = c //Store reference to button text element
                    }

                }

            }

        }

    }

    /** Get interface */ get interface() { return this._interface }
    /** Get text */      get text()      { return this._text }

    /**
     * Click handler for bet
     * @param {object} this_ - Reference to class's scope
     */
    click(this_) {

        let _this = this_ //Store reference to scope

        document.getElementById(arch).classList.add(hidden) //Hide portrait arch
        _this._interface.show = true                        //Toggle bet interface
        _this._interface.render()                           //Render bet interface

    }

    /** Set text */ 
    set text(v_) { 

        let digits = v_.match(/\d+/g).join('') //Retrieve length of digits from currency value

        //If digits is in the millions
        if (digits.length > 6) {

            //If IE/Edge browser
            if (IS_IE) {

                //Apply styling to prevent text overlapping boundary
                this.textElem.style.padding     = '5px'
                this.textElem.style.paddingLeft = '20px'

            }

            //Any other half decent browser
            else {
                this.textElem.style.padding = '5px' //Apply styling to prevent text overlapping boundary    
            }

            
        }

        this._text = v_ //Update text field
        
        this.textElem.innerHTML = this._text //Update button text

    }
 
 }