import { ICONS } from '../../constants/icons/icons.constant'

/**
 * @file Defines the {@link MENU_BUTTON_CONFIG} constant
 * @author Joe Thompson <joe.thompson@1x2network.com>
 * @type {object}
 * @constant
 * @default
 * @property {menu-button-config-setting} home         - Home menu button config.
 * @property {menu-button-config-setting} guide        - Game guide button config.
 * @property {menu-button-config-setting} payout       - Payout button config.
 * @property {menu-button-config-setting} winWaysLines - Win ways/lines button config.
 * @property {menu-button-config-setting} history      - Game history button config.
 * @property {menu-button-config-setting} settings     - Settings button config.
 * @property {menu-button-config-setting} freespins    - Buy freespins button config.
 */
 export const MENU_BUTTON_CONFIG = {

     home: {
 
         img:      ICONS.home,
         optional: { check: { manager: 'framework', func: 'isTouch' }, id: 'home' },
         text:     [ 'Close the game and navigate to the lobby' ]
 
     },    
     
     guide: {
 
         img:  ICONS.gameGuide,
         text: [ 'View help guide and controls' ],
 
     },
     
     payout: {
 
         img:  ICONS.payout,
         text: [ 'View symbol payouts' ],
 
     },
 
     winWaysLines: {
 
         img:  ICONS.winLinesWays,
         text: [ 'View winning combinations' ],
         optional: { check: { manager: 'loginVars', func: 'getRegParams', prop: 'jurisdiction', condition: (v_) => { return [ 'CA' ].indexOf(v_.toUpperCase()) > -1 } }, id: 'win-ways' }
 
     },
 
     history: {
 
         img:  ICONS.history,
         text: [ 'View previous bet details' ]
 
     },
 
     settings: {
         
         img:  ICONS.settings,
         text: [ 'View in-game settings' ]
 
     },
 
     freespins: {
 
         custom:   true,
         img:      ICONS.freespins,
         optional: { check: { manager: 'communicationManager_', func: 'getResumeStatus', prop: 'disableBuyIn' }, id: 'buy-freespins' },
         text:     [ 'Purchase freespins in-game' ],
 
     }

 }