import { AUTOSPIN_INTERFACE_BUTTONS } from '../../../../constants/autospinInterfaceButton/autospin-interface-buttons.constant'
import { AUTOSPIN_VALUES }            from '../../../../constants/autospin-values/autospin-values.constant'
import { AutospinInterfaceButton }    from '../button/autospinInterface.button.class'
import { Click }                      from '../../../click/click.class'

/**
 * @file Defines the {@link AutospinPlus} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /**
  * Class for handling plus button in autospin interface
  * @extends AutospinInterfaceButton
  */
  export class AutospinPlus extends AutospinInterfaceButton {
 
     constructor() {
         super(AUTOSPIN_INTERFACE_BUTTONS.plus) //Call parent constructor
     }
 
     /** Adds click handler to plus button */
     add() {
 
         let plus = document.getElementById(AUTOSPIN_INTERFACE_BUTTONS.plus.id) //Retrieve plus button node
         new Click(plus, this.click, this)                                      //Add click handler
         this.added = true                                                      //Update click handler flag
 
     }
 
     /**
      * Click handler for increasing autospin amount
      * @param {object} this_ - Reference to class's scope 
      */
     click(this_) {
         
         AUTOSPIN_VALUES.increase() //Increase autospin amount
         super.click()              //Call super click
 
     }
 
  }