import { BET_VALUES }      from '../../../../constants/bet-values/bet-values.constant'
import { InterfaceButton } from '../../button/interface.button.class'

/**
 * @file Defines the {@link BetInterfaceButton} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /** 
  * Class for handling bet interface buttons
  * @extends InterfaceButton
  */
  export class BetInterfaceButton extends InterfaceButton {

    /**
     * Initialise content of bet interface button
     * @param {interface-button} b_ - Object containing ID and content of button
     */
    constructor(b_) {
        super(b_) //Call parent constructor
    }

    /**
     * Click handler for bet interface buttons
     */
    click() {

      framework.setBetValue(BET_VALUES.value()) //Update bet value in iron dog framework

      let text = document.getElementById('bet-amount')                        //Retrieve bet amount node
      let b    = balanceManager_.geBalanceObj().currency + BET_VALUES.value() //Store formatted bet amount
 
      text.innerHTML          = b //Update bet amount in interface
      window.newUI.bet().text = b //Update bet button text

    }

  }