import { Click }                 from '../../../click/click.class'
import { BET_VALUES }            from '../../../../constants/bet-values/bet-values.constant'
import { BetInterfaceButton }    from './betInterface.button.class'
import { BET_INTERFACE_BUTTONS } from '../../../../constants/betInterfaceButton/bet-interface-buttons.constant'

/**
 * @file Defines the {@link Max} class.
 * @author Joe Thompson <joe.thompson@1x2network.com>
 */

 /** 
  * Class for handling max bet button in bet interface
  * @extends BetInterfaceButton
  */
 export class Max extends BetInterfaceButton {

    /**
     * Initlaise max bet button
     */
    constructor() {
        super(BET_INTERFACE_BUTTONS.max) //Call parent constructor
    }

    /** Adds click handler to max button */
    add() {

        let max = document.getElementById(BET_INTERFACE_BUTTONS.max.id) //Retrieve max button node
        new Click(max, this.click, this)                                //Add click handler
        this.added = true                                               //Update click handler flag

    }

    /**
    * Click handler for increasing bet value to maximum amount
    * @param {object} this_ - Reference to class's scope
    */
     click(this_) {

        BET_VALUES.max()  //Set to max bet value
        super.click() //Call super click

     }

 }